@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

$bistre: #2B1A0Dff;
$ecru: #DFB97Fff;
$seal-brown: #593012ff;
$smoky-black: #110803ff;
$brown: #9A5A1Dff;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: $ecru;
}

body {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
}

header {
    font-family: 'Pacifico', cursive;
    color: $bistre;
    text-shadow: 0 0 5px $brown;
    text-align: center;
}

main {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: $bistre;
}

main p {
    font-size: 1.2rem;
    text-align: center;
    color: $ecru;
    background-color: $bistre;
    text-shadow: 0 0 5px $brown;
}

footer {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: end;
}

.img-container {
    width: 95vw;
    height: 95vw;
    max-width: 370px;
    max-height: 370px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0 0 5px $smoky-black;
}

.img-container img {
    width: 100%;
    object-fit: cover;
    border-radius: 5%;
}

.footer-img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0 0 5px $smoky-black;
}

.footer-img img {
    width: 100%;
    object-fit: cover;
    border-radius: 5%;
}

.translate {
    transform: translate(0, -150%);
    transition: transform 1s ease-in-out;
}

.hide {
    display: none;
}